import DR from "../Generic/DR";
import { useState, useCallback } from "react";
import { UR, LG } from "../JSON/UR";
import "../Generic/Dr.css";
import Avatar from "@mui/material/Avatar";
const Dr = () => {
  const [cnc, scnd] = useState();
  const [lang, slang] = useState();
  const [wsc, swsc] = useState();
  const {
    dfltCountryCode,
    // dfltCountryName,
    // dfltWhsGroup,
    roles
    // isAdmin,
    // isSuperUser
  } = UR;
  const cnCh = useCallback((e) => {
    scnd(e.value);
  }, cnc);
  const lgCh = useCallback((e) => {
    slang(e.value);
  }, lang);
  const wsCh = useCallback((e) => {
    swsc(e.value);
  }, wsc);
  const cd = roles.map((i) => ({ label: i.countryName, value: i.countryCode }));
  let ws = roles.find((i) => i.countryCode === dfltCountryCode).whsGroup;
  ws = Object.entries(ws).map((i) => ({
    label: i[1].split(":")[0],
    value: i[1].split(":")[1]
  }));
  const lng = LG.filter((i) => i.value === dfltCountryCode || i.value === "EN");
  console.log(cnc, "hiiiii", lang, wsc);
  return (
    <div id="dr" className="dr">
      <div className="cn">
        <Avatar className="hd_ic" />
        <DR options={cd} defaultOption={cnc} onChange={(e) => cnCh(e)} />
      </div>
      <div className="lg">
        <DR options={lng} defaultOption={lang} onChange={(e) => lgCh(e)} />
      </div>
      <div className="ws">
        <DR options={ws} defaultOption={wsc} onChange={(e) => wsCh(e)} />
      </div>
    </div>
  );
};
export default Dr;
